import * as d3 from 'd3'

// https://bl.ocks.org/TamayoValencia/996ca33b7d42d7dff4d69ed00a5fd571



// D3 code
let svg = d3.selectAll('.viz'),
  margin = { top: 20, right: 20, bottom: 30, left: 50 },
  width = +svg.attr('width') - margin.left - margin.right,
  height = +svg.attr('height') - margin.top - margin.bottom,
  g = svg.append('g').attr('transform', 'translate(' + margin.left + ',' + margin.top + ')')

g.append('g')
  .attr('class', 'x axis')

g.append('g')
  .attr('class', 'y axis')

let x = d3.scaleBand()
  .padding(0.1)
  .range([0, width])

let y = d3.scaleLinear()
  .range([height, 0])

export function update (myData) {
  x.domain(myData.map(d => d.name))
  y.domain([0, d3.max(myData, d => d.avg)])

  let points = g.selectAll('.point')
    .data(myData) // Update

  let pointsEnter = points
    .enter()
    .append('rect')
    .attr('class', 'point')

  points.merge(pointsEnter) // Enter + Update
    .attr('x', d => x(d.name))
    .attr('y', d => y(d.avg))
    .attr('width', d => x.bandwidth())
    .attr('height', d => height - y(d.avg))

  points.exit()
    .remove()

  g.select('.x.axis')
    .call(d3.axisBottom(x))
    .attr('transform', 'translate(0, ' + height + ')')

  g.select('.y.axis')
    .call(d3.axisLeft(y))
  g.append('text')
    .attr('y', -15)
    .attr('x', 7)
    .attr('dy', '0.5em')
    .attr('text-anchor', 'end')
    .attr('class', 'graph-text')
    .text('No2')
}

