import * as d3 from 'd3'

// Importing whole files
import './intro.js'
import './backgroundChange.js'
import './titlesAppear.js'
import './scrollMagic.js'
import './form.js'

// Importing polyfill to make async functions work in Parcel
import "babel-polyfill"

// Importing functions
import { transformData, calcMaximumAverage, calcAverage } from './transformData.js'
import { update } from './barChart.js'

Promise.all([
  d3.json('./data/amsterdam_NO2_20190101.json'),
  d3.json('./data/amsterdam_NO2_20190102.json'),
  d3.json('./data/amsterdam_NO2_20190103.json'),
  d3.json('./data/amsterdam_NO2_20190104.json'),
  d3.json('./data/amsterdam_NO2_20190105.json'),
  d3.json('./data/amsterdam_NO2_20190106.json'),
  d3.json('./data/amsterdam_NO2_20190107.json')
]).then((data) => {
  // data.forEach(dataDay => { console.log('raw data: ', dataDay) })

  let transformedData = transformData(data)
  // console.log('all data transformed: ', transformedData)

  matchData(transformedData)

  let weekAverage = calcAverage(transformedData)
  // console.log('weekAverage: ', weekAverage)

  let dayAverage = calcAverage(transformedData[0])
  // console.log('dayAverage: ', dayAverage)

  let maximumAverageWeek = calcMaximumAverage(transformedData)
  // console.log('maximumAverageWeek', maximumAverageWeek)

  let maximumAverageDay = calcMaximumAverage(transformedData[0])
  // console.log('maximumAverageDay', maximumAverageDay)

  let myData = [
    {
      name: 'Amsterdams gemiddelde',
      avg: dayAverage
    },
    { name: 'Woonplaats',
      avg: maximumAverageDay
    }
  ]

  update(myData)

}).catch((err) => {
  console.log('Error loading data!, ', err)
})

function matchData(data) {
  let dataHour = data[0][0].dataArray
  console.log(dataHour)

  getAddress('kinkerstraat').then(data => {
    console.log('found address: ', data)

    let lat = Number(data.geometry.location.lat.toString().slice(0, -1))
    let long = Number(data.geometry.location.lng.toString().slice(0, -1))

    let foundItem = dataHour.filter((item, i) => {
      if (item.long > long - 0.001 && item.long < long + 0.001 && item.lat > lat - 0.001 && item.lat < lat) {
        return item
      }
    })
    console.log('found item(s): ', foundItem)
  })
}

// Google Geocoding async fetch
async function getAddress (address) {
  let response = await (await fetch(`https://maps.googleapis.com/maps/api/geocode/json?address=${address},Amsterdam&key=AIzaSyBdnUS_nOQpLxzrqgqTujJx-Kq6-JaNd6Y
  `))
  let data = await response.json()
  let returnedData = data.results[0]
  if (!returnedData.address_components[6]) {
    window.alert('Adres niet gevonden. Zorg dat het adres goed is geschreven en zich binnen Amsterdam bevindt.')
    return null
  } else {
    return returnedData
  }
}
